@media (max-width: 1500px) and (min-width: 660px) {
    .block {
        width: 45% !important;
        height: 140px !important;
    }
    .block * {
        font-size: 1.5rem !important;
    }

    .block-news {
        width: 100% !important;
        height: 140px !important;
    }
    .block-news * {
        font-size: 1.4rem !important;
    }

    .maintitle {
        font-size: 1.75rem !important;
    }

    .alertSALE .MuiPaper-root {
        width: 60% !important;
    }
}
@media (max-width: 660px) and (min-width: 0px) {
    .block {
        width: 100% !important;
        height: 100px !important;
    }
    .block * {
        font-size: 1.35rem !important;
    }

    .block-news {
        height: 140px !important;
    }
    .block-news * {
        font-size: 1.25rem !important;
    }

    .maintitle, .title {
        width: 100% !important;
        text-align: center;
        font-size: 1.4rem !important;
    }
    .maintitle {
        border-radius: 0px !important;
    }

    .subheader-menu {
        display: flex;
        width: 100% !important;
        justify-content: center !important;
    }
    .subheader-menu button {
        width: 100% !important;
        font-size: 0.9rem !important;
    }
    .subheader-other button {
        width: 48% !important;
        font-size: 0.9rem !important;
    }
    .button-default {
        width: 100% !important;
        font-size: 0.9rem !important;
    }
    .header-right, .menuIconHeader {
        width: 100% !important;
    }
    .alertSALE .MuiPaper-root {
        width: 90% !important;
    }
    .MuiContainer-root {
        padding: 0px !important;
    }
    .sale-block {
        border-radius: 0px !important;
    }
    footer {
        border-radius: 0px !important;
        padding: 0.5rem 1rem !important;
    }

    .header-right button, .subheader-menu button {
        border: none !important;
        border-bottom: 1px solid var(--buttonBorderColor) !important;
        border-radius: 0px !important;
    }
    .topheader, .topheader .header-right, .header, .subheader-other {
        gap: 0px !important;
    }
    .subheader {
        gap: 4px !important;
    }
    .subheader button {
        border-radius: 0px !important;
    }
    main {
        margin-top: 12px !important;
    }
    .summaryBasket, .basketfinal{
        width: 100% !important;
    }
    .error-page {
        padding: 1rem !important;
    }
    .MuiToggleButton-root {
        font-size: 0.75rem !important;
        padding: 5px !important;
    }
}

@media (max-width: 843px) {
    .subheader-menu {
        border: none !important;
    }
    .subheader-other {
        width: 100% !important;
        justify-content: space-between;
    }
}