.maintitle {
    width: max-content !important;
    background-color: var(--bgHeaderMain) !important;
    border-radius: 0px 0px 20px 20px;
    color: var(--colorWhite) !important;
    padding: 10px 20px !important;
}
.maintitlehelp {
    width: max-content !important;
    background-color: var(--bgHeaderMain) !important;
    border-radius: 10px;
    color: var(--colorWhite) !important;
    writing-mode: vertical-lr !important;
    font-size: 1.7rem !important;
    padding: 5px 10px !important;
}

.subheader button {
    background-color: var(--bgSecond) !important;
    border: 2px solid var(--helpColor) !important;
    border-radius: 10px !important;
    text-transform: unset !important;
    font-size: 1.2rem !important;
}
.subheader-other button {
    border: none !important;
    font-size: 1.1rem !important;
}

.button-default {
    background-color: var(--bgSecond) !important;
    border: 2px solid var(--helpColor) !important;
    border-radius: 10px !important;
    text-transform: unset !important;
    font-size: 1.1rem !important;
}


.container {
    background-color: var(--bgBody) !important;
    border-radius: 10px !important;
    padding: 1rem !important;
}
.container.no-bg h5 {
    margin-bottom: 1rem;
}
.no-bg {
    background-color: transparent !important;
}

.sale-block {
    cursor: pointer;
    padding: 0.5rem 1rem !important;
    transition: all 0.15s ease;
}
.sale-block:hover {
    background-color: var(--helpColor) !important;
}
.sale-block * {
    font-size: 1.2rem !important;
}

.icon-sb {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.block {
    cursor: pointer;
    width: 270px;
    height: 220px;
    background-color: var(--bgBlock);
    border-radius: 25px;
    padding: 1rem;
    transition: all .3s ease;
}
.block * {
    color: var(--colorBlack) !important;
}

h4 {
    word-break: break-word !important;
}

.block-news {
    cursor: pointer;
    width: 700px;
    height: 200px;
    background-color: var(--bgBlockNews);
    border-radius: 25px;
    padding: 1rem;
    transition: all .3s ease;
}
.block-news * {
    color: var(--colorBlack) !important;
}

.CardItem {
    cursor: pointer;
    width: 220px;
    height: 380px;
    background-color: var(--bgCardItem);
    border-radius: 24px;
    padding: 1rem;
}
.CardItem .MuiTypography-body2 {
    height: 72px !important;
}
.CardItem button {
    border-radius: 16px;
    background-color: var(--colorWhite) !important;
    color: var(--color) !important;
    padding: 0.5rem 1rem;
}

.title {
    cursor: pointer;
}

.over-scroll {
    overflow-y: scroll;
}

.url-footer h6, .apps-footer img {
    cursor: pointer;
}

footer {
    background-color: var(--bgBody) !important;
    border-radius: 25px 25px 0px 0px !important;
    padding: 1rem 2rem !important;
    margin-bottom: 0px !important;
}

.alertSALE .MuiPaper-root {
    width: 35% !important;
    border-radius: 25px !important;
    padding: 0.5rem 1rem;
}

.minusButton {
    border-radius: 16px 0px 0px 16px !important;
}
.numberButton {
    border-radius: 0px !important;
}
.plusButton {
    border-radius: 0px 16px 16px 0px !important;
}
.minusButton, .numberButton, .plusButton {
    height: 42px !important;
}

.CardItemBasket {
    width: 100% !important;
    padding: 1rem;
}

.basketbutton {
    width: 100% !important;
    background-color: transparent !important;
    color: var(--color) !important;
    border: 2px solid var(--colorBlack) !important;
    border-radius: 0px !important;
    transition: all .3s ease;
}
.basketbutton:hover {
    background-color: var(--colorBlack) !important;
    color: var(--colorWhite) !important;
}

.basketbuttonRemove {
    width: 100% !important;
    background-color: var(--helpColor) !important;
    color: var(--color) !important;
    border-radius: 10px !important;
    transition: all .3s ease;
}
.basketbuttonRemove:hover svg * {
    color: var(--colorWhite) !important;
    fill: var(--colorWhite) !important;
}
.basketbuttonRemove:hover {
    background-color: var(--colorBlack) !important;
    color: var(--colorWhite) !important;
    
}

.basketfinal {
    width: 48% !important; 
}

.prod-news {
    background-image:url("../static/production.png");
    background-color:#ffffff;
    background-repeat:no-repeat;
    background-position:center;
    background-size:cover
}
.del-news {
    background-image:url("../static//delivery.png");
    background-color:#ffffff;
    background-repeat:no-repeat;
    background-position:center;
    background-size:cover
}

.but-group {
    width: max-content !important;
    background-color: transparent !important;
    border: none !important;
    border-radius: 0px !important;
    box-shadow: none !important;
}

.but-group button {
    background-color: var(--bgCardItem) !important;
    color: var(--color) !important;
}

.MuiButtonGroup-grouped:not(:last-of-type) {
    border-color: var(--helpColor) !important;
}

.basketsecondcard {
    white-space: pre-line !important;
}