:root {
    --font: 'Montserrat', sans-serif;
    --color: #000;
    --colorSecond: #c3c3c3;
    --colorOpacity: #000000e0;
    --colorInvert: #fff;
    --colorWhite: #fff;
    --colorBlack: #000;
    --helpColor: #cfcfcf; 
    --bgBody: #fff;
    --bgSecond: #f9f9f9;
    --bgSecondHelper: rgba(0, 0, 0, 0.06);

    
    --bgHeaderMain: #383158; /* aaa7ba */
    --bgBlock: #e5f2ff;
    --bgBlockNews: #fbf2eb;
    --bgCardItem: #ededed;
    --buttonBorderColor: #b3b3b3;
}

